const SAFE_TIMEOUT = 300;

export function addHashByHashValue({ hash, keepLocation = false }) {
    const COLOR_END = '#00FF0022';

    setTimeout(() => {
        if (!keepLocation) {
            location.hash = hash;
            setTimeout(() =>  window.scrollBy(0, -200), 0);
        }
        const hashEl = document.querySelector(hash);
        if (!hashEl) {
            return;
        }
        hashEl.style.background = COLOR_END;
    }, SAFE_TIMEOUT);
}

export function removeHashByHashValue(hash) {
    const hashEl = document.querySelector(hash);
    if (!hashEl) {
        return;
    }
    removeBackgroundFromElem(hashEl);
}


export function removeBackgroundFromElem(hashEl) {
    if (!hashEl) {
        return;
    }
    setTimeout(() => hashEl.style.background = '', SAFE_TIMEOUT);
}
