<template>
    <div v-if="line" class="line-card" :id="line.hash">
        <div class="controls-wrapper">
            <div v-if="!isSearch" class="control-sign" @click="toggleHash">#</div>
            <div v-if="isSearch" class="control-sign" @click="switchToBrowseMode">▤</div>
        </div>
        <div class="lines">
            <GrepLine v-for="({ str, searcher, mainColor, key, isTitleLine }) in dataArray"
                      :str="str"
                      :searcher="searcher"
                      :main-color="mainColor"
                      :key="key"
                      class="mb-1"
            ></GrepLine>
            <div><br></div>
        </div>

    </div>
</template>

<script>
import GrepLine from '@/components/corpusWithTranslation/GrepLine';
import { DisplayModes, LineNames } from '@/components/corpusWithTranslation/constants';
import { addHashByHashValue, removeBackgroundFromElem, removeHashByHashValue } from '@/services/addOrRemoveHash';
import { emptify } from '@/services/emptifyObject';


export default {
    name: "LineCard",
    props: {
        hasHash: { default: false },
        line: { required: true },
        searchers: { default: () => ({}) },
        mainColors: {
            default: () => ({
                titleLine: 'purple',
                textus: 'black',
                versio: 'darkblue',
            }),
        }
    },
    data() {
        return {
            LineNames: LineNames,
        }
    },
    components: {
        GrepLine,
    },
    computed: {
        dataArray() {
            return [this.LineNames.titleLine, this.LineNames.textus, this.LineNames.versio].map((key, i) => ({
                str: this.line[key],
                searcher: this.searchers[key],
                mainColor: this.mainColors[key],
                key,
                get isTitleLine() {
                    return key === LineNames.titleLine;
                }
            }))
        },
        isSearch() {
            return !_.isEmpty(emptify(this.searchers));
        },
    },
    methods: {
        switchToBrowseMode() {
            if (!this.isSearch) {
                return;
            }
            const { volumeName, textNumber } = this.line;
            this.$router.push({
                query: {
                    mode: DisplayModes.browse,
                    volume: volumeName,
                    text: textNumber,
                },
            });
            this.toggleHash();
        },
        toggleHash() {
            if (!this.$route.hash) {
                addHashByHashValue({ hash: '#' + this.line.hash });
                return;
            }

            removeHashByHashValue(this.$route.hash);

            if (this.$route.hash === '#' + this.line.hash) {
                this.$router.replace({ ...this.$route, hash: '' });
            } else {
                addHashByHashValue({ hash: '#' + this.line.hash, keepLocation: true });
                this.$router.replace({ ...this.$route, hash: '#' + this.line.hash });
            }

            const allElems = Array.from(document.querySelectorAll('div.line-card[id^="h"]'));
            for (const elem of allElems) {
                if (elem.style.background && elem.id !== this.line.hash) {
                    removeBackgroundFromElem(elem);
                }
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import "../../styles/variables.scss";

.line-card {
    display: flex;
}
.controls-wrapper {
    width: $small-box-width;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}


.control-sign {
    font-size: 25px;
    color: #eae9e9;
    cursor: pointer;
    &:hover {
        color: #757575;
        font-weight: bold;
    }
    &:first-child {
        margin-bottom: 10px;
    }
}
.lines {
    flex: 1;
}
</style>
